<template>
  <div class="issue view">
    <Panel title="问题列表">
      <div class="list">
        <el-table
          v-loading="loading"
          element-loading-text="加载中"
          ref="table"
          :data="issueList"
          style="width: 100%">
          <el-table-column
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column
            prop="id"
            label="ID"
            sortable
            width="100">
          </el-table-column>
          <el-table-column
            prop="title"
            label="标题"
            sortable
            width="220"
            show-tooltip-when-overflow>
          </el-table-column>
          <el-table-column
            prop="created_at"
            sortable
            label="提问时间">
          </el-table-column>
          <el-table-column
            inline-template
            sortable
            label="提问者">
            <span @click="showUserInfo(row.wx_users)" :class="{'click_able': row.wx_users !== null}">{{row.wx_users !== null ? row.wx_users.name : '匿名'}}</span>
          </el-table-column>
          <el-table-column
            prop="description"
            sortable
            label="内容">
          </el-table-column>
          <el-table-column
            inline-template
            fixed="right"
            label="操作"
            width="110">
            <span>
              <el-button type="text" size="small" @click="replay(row.id)">回复</el-button>
              <el-button type="text" size="small" @click="deleteIssue(row.id)">删除</el-button>
            </span>
          </el-table-column>
        </el-table>
        <div class="option">
          <el-pagination
            class="page"
            layout="prev, pager, next"
            @current-change="currentChange"
            :page-size = "pageSize"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </Panel>
    <el-dialog :title="currIssue.title" v-model="replayDialog" size="tiny">
      <p>{{currIssue.description}}</p>
      <el-input type="textarea" placeholder="回复" :rows="2" v-model="answer"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="replayDialog = false">取 消</el-button>
        <el-button type="primary" @click="replayDialog = false, replayAnswer()">回 复</el-button>
      </span>
    </el-dialog>
    <el-dialog :title="currUser.name" v-model="userInfoDialog" size="tiny">
      <el-row :gutter="20" class="user_info_body">
        <el-col :span="12" class="item">
          <label>ID</label>
          <span>{{currUser.id}}</span>
        </el-col>
        <el-col :span="12" class="item">
          <label>email</label>
          <span>{{currUser.email != null ? currUser.email : '未填'}}</span>
        </el-col>
        <el-col :span="12" class="item">
          <label>姓名</label>
          <span>{{currUser.name}}</span>
        </el-col>
        <el-col :span="12" class="item">
          <label>毕业学校</label>
          <span>{{currUser.old_school != null ? currUser.old_school : '未填'}}</span>
        </el-col>
        <el-col :span="12" class="item">
          <label>电话</label>
          <span>{{currUser.phone}}</span>
        </el-col>
        <el-col :span="12" class="item">
          <label>成绩</label>
          <span>{{currUser.course != null ? currUser.course : '未填'}}</span>
        </el-col>
        <el-col :span="12" class="item">
          <label>地区</label>
          <span>{{currUser.province != null ? currUser.province + ' ' + currUser.city + ' ' + currUser.area : '未填'}}</span>
        </el-col>
      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button @click="userInfoDialog = false">关 闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import api from '../api'
  import Panel from '../components/Panel.vue'
  export default {
    components: {
      Panel
    },
    data () {
      return {
        loading: false,
        issueList: [],
        total: 0,
        answer: '',
        // 一页显示的数
        pageSize: 10,
        replayDialog: false,
        currIssue: {},
        currentPage: 0,
        currUser: {},
        userInfoDialog: false
      }
    },
    methods: {
      showUserInfo (user) {
        if (user !== null) {
          this.userInfoDialog = true
          this.currUser = user
        }
      },
      currentChange (page) {
        this.currentPage = page
        this.getIssueList((page - 1) * this.pageSize, this.pageSize)
      },
      getIssueList (offset, limit) {
        api.getIssueList(offset, limit).then(res => {
          this.issueList = res.data
        }, res => {
          if (res.status === 401) {
            api.refreshToken().then(res => {
              api.getIssueList(offset, limit).then(res => {
                this.issueList = res.data
              })
            })
          }
        })
      },
      replay (id) {
        this.replayDialog = true
        for (let i = this.issueList.length - 1; i >= 0; i--) {
          if (this.issueList[i].id === id) {
            this.currIssue = this.issueList[i]
            break
          }
        }
      },
      replayAnswer () {
        api.replay(this.currIssue.id, this.answer).then(res => {
          this.$message('已回复')
          this.getIssueList((this.currentPage - 1) * this.pageSize, this.pageSize)
          api.getIssueListCount().then(res => {
            this.total = Number(res.data)
          })
        })
      },
      deleteIssue (id) {
        api.deleteIssue(id).then(res => {
          this.getIssueList((this.currentPage - 1) * this.pageSize, this.pageSize)
          api.getIssueListCount().then(res => {
            this.total = Number(res.data)
          })
          this.$notify({
            title: '提示',
            message: '该问题已经被放入回收站，你可以在回收站中找回该问题。'
          })
        })
      }
    },
    mounted () {
      api.getIssueListCount().then(res => {
        this.total = Number(res.data)
      })
      this.getIssueList(0, this.pageSize)
    }
  }
</script>
<style lang="less" scoped>
.issue {
  .user_info_body{
    padding: 0 15px;
    .item{
      label{
        text-align: right;
        vertical-align: middle;
        float: left;
        font-size: 14px;
        color: #5e6d82;
        line-height: 1;
        padding: 11px 12px 11px 0;
        box-sizing: border-box;
        font-weight: bold;
      }
      span{
        line-height: 36px;
        position: relative;
        font-size: 14px;
      }
    }
  }
  .option{
    border: 1px solid #e0e6ed;
    border-top: none;
    padding: 10px;
    background-color: #fff;
    position: relative;
    height: 53px;
    button{
      margin-right: 10px;
    }
    >.page{
       position: absolute;
       right: 20px;
       top: 10px;
     }
  }
  .click_able{
    color: #0078D7;
    cursor: pointer;
  }
}
</style>


